.home__viwer-container, .home__org-container {
  height: 100%;
  padding: 20px 20px 0 20px;
}

.home__viwer-title, .home__viwer-title--blue {
  margin: 0 0 20px 0;
  font-size: 19px;
  text-align: center;
  font-weight: 400;
}

.home__viwer-title--blue {
  color: #367BCF;
  font-weight: 500;
  cursor: pointer;
}

.add-button__icon {
	position: absolute;
  bottom: 90px;
  right: 20px;
}

.home__icon{
  position: fixed;
  right: 20px;
  z-index: 10;
  bottom: 80px;
}

.home__info-description{
  font-size: 20px;
  line-height: 1.4;
  padding: 15px;
  border: 1px solid #CCCCCC;
  border-radius: 8px;
  max-height: 100%;
  margin-top: 30px;
  text-align: center;
}