.players__container {
    padding: 20px 20px 150px 20px
  }

  .players__search{
    margin-bottom: 20px;
  }

  .noplayers-container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0 20px 0;
  }
  
  .noplayers-text{
    font-weight: 500;
    font-size: 17px;
    text-align: center;
  }
  
  .players__block {
    border-radius: 11px;
    border: 1px solid #CCCCCC;
  }
    
  .players__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-bottom: 1px solid #CCCCCC;
  }
  
  .players__wrapper:last-of-type {
    border-bottom: none;
  }
  
  .players__name-container {
    width: 90%;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right: 5px;
    overflow: hidden;
  }
  
  .players__number-container {
    background-color: #EBF4FF;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 15px 0 0;
  }
  
  .players__number {
    color: #3A3A3A;
    font-size: 19px;
    margin: 0;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .players__name {
    color: #3A3A3A;
    font-size: 19px;
    margin: 0;
    font-weight: 400;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  
  .players__delete-button {
    background-color: #FFEBEB;
    width: 25px;
    height: 25px;
    padding: 3px;
    border-radius: 100%;
    cursor: pointer;
  }