.stage-group__header {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin: 0 0 15px 0;
}

.stage-group__name-container{
  display: flex;
  align-items: center;
}

.stage-group__type-icon {
  width: 25px;
  height: 25px;
  margin: 0 10px 0 0;
}

.edition-icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.stage-group__name {
  font-weight: 500;
  font-size: 19px;
  /* margin: 0 15px 10px 0; */
  margin: 0 15px 0 0;
  display: flex;
  align-items: center;
}

.stage-group__type {
  box-sizing: border-box;
  height: 45px;
  width: 100%;
  border: 1px solid #CCCCCC;
  border-radius: 9px;
  line-height: 2.5;
  padding-left: 10px;
  font-size: 18px;
  color: #3A3A3A;
  margin: 5px 0 5px;
}

.stage-group__select{
  margin: 5px 0;
}

.stage-group__name-edition{
  display: flex;
  /* margin: 0 0 15px 0; */

  margin: 0 0 0 0;
}

.stage-group__input-edition{
  border-radius: 6px;
  border: 1px solid #CCCCCC;
  margin-right: 20px;
  width: 70%;
  padding-left: 10px;
  font-size: 20px;
}

.stage-group__icon-done{
  height: 33px;
  width: 33px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  background-color: #EBF4FF;
  cursor: pointer;
}

.stage-group__icon-deletion{
  margin-bottom: 10px;
}

.icon-deletion {
  cursor: pointer;
}

.bucket__icon {
  cursor: pointer;
}

.stage-group__popup-participants-name {
  padding: 15px 0;
  /* border-bottom: 1px solid #CCCCCC; */
  font-size: 20px;
}

.stage-group__popup-participants-doubles{
  display: flex;
  align-items: center;
  padding: 10px 0;
  font-size: 20px;
}

.stage-group__popup-doubles-rating{
  width: 45px;
  background-color: transparent;
}

.stage-group__popup-participants-name--first{
  margin-bottom: 10px;
}

.stage-group__participants-addition{
  padding: 10px 0 15px 0;
  font-size: 19px;
  color: #4694D9;
  font-weight: 500;
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.stage-group__popup-participants-item{
  border-bottom: 1px solid #CCCCCC;
  display: flex;
  justify-content: space-between;
  padding: 0 10px 0 10px;
  cursor: pointer;
}

.stage-group__popup-participants-item--selected {
  background-color: #ebf4ff;
  cursor: pointer;
}

.stage-group__players-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;
  width: 100%;
}

.arrow--up {
  transform: rotate(180deg);
  transition: all 0.4s;
}

.stage-group__players{
  width: 100%;
}

.stage-group__players-subtitle{
  font-weight: 500;
  font-size: 19px;
  color: #3A3A3A;
  margin: 10px 0 0 0;
}

.stage-group__components{
  display: flex;
  align-items: center;
  width: 100%;
}

.stage-group__number-container{
  display: flex;  
  align-items: center;
  height: 120px;
}

.stage-group__number-container:last-child{
  border-bottom: none;
}

.stage-group__number{
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: #EBF4FF;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin-right: 15px;
}

.delete-player{
  margin: 15px 0 5px 0;
  font-size: 19px;
  color: #4694D9;
  font-weight: 500;
  display: flex;
  justify-content: center;
}