.tournament__participants-title {
  font-size: 20px;
  font-weight: 500;
}

.popup__input-player--first,
.popup__input-player--second {
  margin-bottom: 15px;
}

.popup-button__addition-doubles {
  margin-bottom: 10px;
}

.popup-search__list-item {
  font-size: 20px;
  padding: 15px 10px;
  border-bottom: 1px solid #CCCCCC;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.popup-search__player-name{
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.popup-search__player-name > span {
  font-size: 15px;
  margin-right: 5px;
}

.popup__limit-title {
  color: #707070;
  font-size: 16px;
  margin: 15px 0 0 0;
}

.popup-search__list-item-status{
  font-size: 14px;
  color: #4694D9;
  margin-left: 10px;
}

.popup-search__list-item:first-of-type{
  margin-top: 15px;
}

.participants__button-addition {
  margin-top: 20px;
}

.participants {
  margin-top: 20px;
  border: 1px solid #CCCCCC;
  border-radius: 11px;
}

.participants-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #CCCCCC;
  padding: 0 15px;
  font-size: 20px;
}

.participants-item:last-child {
  border-bottom: 0px solid #CCCCCC;
}

.participants-item--double {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #CCCCCC;
  padding: 15px 15px;
  font-size: 20px;
}

.participants-item--double:last-child {
  border-bottom: 0px solid #CCCCCC;
}

.participants__number {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  background-color: #EBF4FF;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 15px 0 0;
  font-size: 18px;
}

.participants__names {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  font-size: 20px;
  padding: 5px 0;
  overflow: hidden;
}

.participants__name {
  margin: 20px 0;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.participants__palyer--first,
.participants__palyer--second {
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.participants__palyer--first {
  margin-bottom: 10px;
}

.participants-icons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.participants__icon-edit {
  display: block;
  margin-bottom: 10px;
  cursor: pointer;
}

.participants__button-save{
  box-sizing: border-box;
  width: calc(100vw - 15px);
  position: fixed;
  padding: 0 10px;
  bottom: 80px;
  left: 0px;
  right: 0px;
  margin: auto;
}