.tournament__components {
	margin: 10px 0 80px 0;
}

.tournament-result__participants-list {
	
}

.tournament-result__participants-list-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	cursor: pointer;
}

.tournament-result__participants-list-title {
	font-size: 20px;
	font-weight: 500;
}

.tournament-result__participants-list-container {
	border: 1px solid #b8b8b8;
	border-radius: 11px;
	margin-top: 10px;
}

.tournament-result__participants-wrapper {
	padding: 15px;
	display: flex;
	align-items: center;
	border-bottom: 1px solid #CCCCCC;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	cursor: pointer;
}

.tournament-result__participants-list-raiting-container{
	width: 30px;
	height: 30px;
	margin-right: 10px;
}

.tournament-result__participants-list-raiting {
	width: 30px;
	height: 30px;
	border-radius: 100%;
	background-color: #EBF4FF;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 20px;
}

.tournament-result__participants-list-name {
	font-size: 20px;
	padding: 15px;
}

.tournament-result__participants-wrapper:last-child {
	border-bottom: 0px;
}

.tournament-result__description-container {
	margin: 30px 0 0 0;
}

.tournament-result__description-title {
	font-size: 20px;
	font-weight: 500;
	margin: 0 0 15px 0;
}

.tournament-result__description-block {
	padding: 15px;
	border: 1px solid #CCCCCC;
	border-radius: 9px;
}

.description-block__text {
	color: #3A3A3A;
	font-size: 19px;
	margin: 0 0 0 0;
	font-weight: 400;
}