.nocontent__section {
  padding: 20px;
  border: 1px solid #CCCCCC;
  border-radius: 11px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.nocontent__title {
  font-size: 20px;
  margin: 0 0 0 0;
  font-weight: 500;
  text-align: center;
}

.nocontent__text{
  font-size: 19px;
  margin: 0 0 0 0;
  font-weight: 400;

}

.nocontent__paragraph{
  display: flex;
  align-items: center;
  margin-top: 20px;
}

img{
  display: block;
  margin-right: 10px;
  height: 25px;
}