.popup__container {
  width: 100%;
  /* height: 100%; */
  display: flex;
  background-color: rgba(0, 0, 0, 0.29);
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  z-index: 9999;

  height: 100dvh;
}

.popup__wrapper {
  width: 100%;
  background-color: #fff;
  border-radius: 11px;
  padding: 20px;
  margin: 20px;

  overflow: auto;
  max-height: -webkit-fill-available;
}

.popup__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 20px 0;
}

.popup__title {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0;
  font-weight: 500;
}

.popup__close-icon {
  cursor: pointer;
  padding: 10px;
}

.popup__body {
  /* overflow-y: scroll;
  max-height: 430px; */

  overflow: auto;
}

.popup__body::-webkit-scrollbar {
  width: 0;
}

.react-datepicker-wrapper:first-child {
  margin-bottom: 15px;
}