.tournaments-list {
  border: 1px solid #CCCCCC;
  border-radius: 11px;
  margin-bottom: 80px;
}

.list-card {
  border-bottom: 1px solid #CCCCCC;
  cursor: pointer;
}

.list-card:last-child {
  border-bottom: 0px solid #CCCCCC;
}

.card__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 15px 15px 15px;
}

.card__content{
  white-space: nowrap;
  overflow: hidden;
}

.card__head {
  display: flex;
  margin: 0 0 15px 0;
}

.card__period {
	font-size: 16px;
  margin: 0 10px 0 0;
}

.card__category {
  margin: 0 0 0 0;
  font-size: 16px;
}

.card__name {
	font-size: 20px;
  margin: 0 0 0 0;
  font-weight: 400;
	white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.card__icon {
  transform: scale(0.8);
}
