.input{
  display: block;
  width: 100%;
  border-radius: 9px;
  border: 1px solid #CCCCCC;
  box-sizing: border-box;
  font-size: 17px;
  padding: 17px;
  font-family: 'Rubik', sans-serif;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

input:focus, input:hover{
  outline: none;
  border-color: #367BCF;
}

input::-webkit-calendar-picker-indicator {
  opacity: 0;
}