.venues__wrapper {
  padding: 20px 20px 0 20px;
}

.venues__container {
  border-radius: 15px;
  border: 1px solid #CCCCCC;
}

.venues__novenues-container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0 20px 0;
}

.novenues-text{
  font-weight: 500;
  font-size: 17px;
  text-align: center;
}

.venues {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 11px;
  border: solid #CCCCCC;
  border-top-width: 0;
  border-right-width: 0;
  border-bottom-width: 1px;
  border-left-width: 0;
}

.venues:last-child {
  border: 0;
}

.venues__block {
  width: 90%;
  display: flex;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  margin-right: 5px;
}

.venues-item__number-container {
  margin: 0 15px 0 0;
}

.venues-item__number {
  color: #3A3A3A;
  font-size: 19px;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #EBF4FF;
  border-radius: 100%;
}

.venues-item__name {
  color: #3A3A3A;
  font-size: 19px;
  margin: 0;
  font-weight: 400;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.venues__delete-button{
  background-color: #FFEBEB;
  width: 25px;
  height: 25px;
  padding: 3px;
  border-radius: 100%;
}

.venues__delete-button__icon {
  width: 25px;
  height: 25px;
  cursor: pointer;
}