.tournament-container{
  padding: 0 15px 15px 15px;
}

.tournament-section{
  margin-bottom: 30px;
}

.tournament-section:last-child {
  margin-bottom: 0px;
}

.section-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section-header__title{
  margin: 0;
  font-size: 20px;
  font-weight: 500;
}

.tournament__components{
  margin: 10px 0 0 0;
}

.stages-menu__item{
  display: flex;
  justify-content: center;
  align-items: center;
}

.stages-menu__item-icon{
  height: 30px;
  width: 35px;
}

.tournament__stages-tab, .button-stage-addition{
  border: 1px solid #4694D9;
  background-color: #fff;
  height: 35px;
  border-radius: 9px;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  margin-right: 15px;
}

.tournament__stage-menu{
  display: flex;
  justify-content: center;
  align-items: center;
}

.tournament__stages-tab--chosen{
  border: 1px solid #4694D9;
  background-color: #4694D9;
  color: white;
  height: 35px;
  border-radius: 9px;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  margin-right: 15px;
  cursor: pointer;
}

.button-stage-addition{
  font-size: 27px;
  color: #4694D9;
  cursor: pointer;
  box-sizing: border-box;
  background-color: #fff;
}

.arrow--down, .arrow--up {
  cursor: pointer;
}

.tournament__stage-addition--active{
  height: 35px;
  border-radius: 9px;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  font-size: 27px;
  color: white;
  background-color: #4694D9;
  border: 1px solid #4694D9;
}

.tournament-footer__container {
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 15px;
  box-shadow: 0px 0px 36px rgba(154, 154, 154, 0.25);
  border-radius: 20px 20px 0px 0px;
}

.tournament__footer-menu{
  display: flex;
  justify-content: space-between;
}

.tournament__footer-menu--opened{
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.tournament-footer__link {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.tournament-footer__link > img {
  margin: 0;
}

.cup-icon {
  width: 28px;
  height: 28px;
}