.button-structure {
  width: 100%;
  padding: 20px 5px 20px 5px;
  background-color: white;
  border: 1px solid #367BCF;
  border-radius: 11px;
  font-size: 19px;
  font-weight: 500;
  color: #367BCF;
}

.button-structure__light {
  width: 100%;
  padding: 20px 5px 20px 5px;
  background-color: white;
  border: 1px solid #367BCF;
  border-radius: 11px;
  font-size: 19px;
  font-weight: 500;
  color: #367BCF;
  box-shadow: 0 0 7px 2px rgba(113, 176, 235, 0.5);
}

.button-structure, .button-structure__light {
  cursor: pointer;
}