.match__wrapper {
  width: 100%;
}

.match__icon-cross {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0.5vh;
  cursor: pointer;
}

.match__container {
  padding: 0 1.5vh 2vh;
  margin-bottom: 2vh;
}

.match__set {
  border: 1px solid #CCCCCC;
  border-radius: 11px;
}

.match__set-score {
  padding: 0 2vh 2vh;
}

.match__set-players__score {
  margin: 0 0 3vh 0;
}

.match__set-players__names {
  display: flex;
  justify-content: space-around;
}

.match__set-players__scores {
  display: flex;
  justify-content: space-around;
}

.match__set-players__name--first,
.match__set-players__name--second {
  font-size: 19px;
  text-align: center;
  width: 50%;
  padding: 2vh 0.1vh;
}

.match__set-players__name--second {
  border-left: 1px solid #CCCCCC;
}

.match__set-players__scores-item--first,
.match__set-players__scores-item--second {
  font-size: 50px;
  text-align: center;
  width: 50%;
  padding: 2vh 0.1vh;
}

.match__set-players__scores-item--second {
  border-left: 1px solid #CCCCCC;
}

.match__set-players__scores-item--extra{
  font-size: 30px;
  color: #3A3A3A;
  font-weight: 500;
}

.match__set-players__buttons {
  display: flex;
  justify-content: space-around;
  margin-bottom: 3vh;
}

.match__set-players__buttons-item {
  display: flex;
}

.match__set-players__buttons-minus {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 6vh;
  height: 6vh;
  background-color: #FFEBEB;
  border-radius: 8px;
  border: 1px solid #CF3636;
  margin-right: 1.5vh;
  cursor: pointer;
}

.match__set-players__icon-minus {
  width: 3vh;
}

.match__set-players__buttons-plus {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 6vh;
  height: 6vh;
  background-color: #EBF4FF;
  border-radius: 8px;
  border: 1px solid #367BCF;
  cursor: pointer;
}

.match__set-buttons{
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 3vh;
}

.match__set-button--six{
  width: 14vh;
  height: 40px;
  background-color: #EBF4FF;
  color: #367BCF;
  font-size: 20px;
  font-weight: 500;
  border: 1px solid #367BCF;
  border-radius: 9px;
}

.match__set-taibreak {
  font-size: 19px;
  font-weight: 500;
  background-color: transparent;
  border: none;
  display: block;
  margin: 20px auto;
  color: #367BCF;
}

.match__set-taibreak--active{
  font-size: 19px;
  font-weight: 500;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #367BCF;
  display: block;
  margin: 20px auto;
  color: #367BCF;
}

.match__sets {
  margin-top: 25px;
}

.match__sets-table {
  border: 1px solid #CCCCCC;
  border-radius: 11px;
}

.match__sets-table__sets {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #CCCCCC;
}

.match__sets-table__sets:first-child {
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.match__sets-table__sets:last-child {
  background-color: #367BCF;
}

.match__sets-table__sets-scores--first,
.match__sets-table__sets-scores--second {
  font-size: 19px;
  text-align: center;
  width: 50%;
  padding: 15px 5px;
  background-color: transparent;
}

.match__sets-table__sets-scores--second {
  border-left: 1px solid #CCCCCC
}

.match__sets-table__buttons-set-addition,
.match__sets-table__buttons-set-deletion {
  width: 50%;
  border: none;
  border: 1px solid #539af0;
  background-color: transparent;
  height: 60px;
  font-size: 19px;
  font-weight: 500;
}

.match__sets-table__buttons-set-deletion {
  border-left: none;
  color: #F20505;
}

.match__sets-table__buttons-set-addition {
  color: #367BCF;
}

.match__sets-table__buttons {
  display: flex;
  justify-content: space-around;
}

.match__sets-table__button-finish {
  width: 100%;
  border: none;
  background-color: transparent;
  height: 60px;
  color: #367BCF;
  font-size: 19px;
  font-weight: 500;
}