.tournaments__tournamnet-creation {
  margin: 0 20px 0 20px;
}

.tournamnet-creation__header {
  padding: 0;
}

.tournamentform-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 19px;
  /* margin: 0 20px 0 20px; */
}

.tournamentform-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.tournamentform-form {
  /* margin: 0 20px 80px 20px; */
  margin-bottom: 80px;

  overflow: hidden;
}

.tournamentform-head {
  width: 95%;
  display: flex;
  align-items: center;
}

.form-input,
.form-select,
.form__inputs-radio {
  margin: 0 0 30px 0;
}

.form__input-date {
  display: block;
  width: 100%;
  border-radius: 9px;
  border: 1px solid #CCCCCC;
  box-sizing: border-box;
  font-size: 17px;
  padding: 17px;
  font-family: "Rubik", sans-serif;
  background-color: transparent;
}

.form__input-date::-webkit-calendar-picker-indicator {
  display: none;
}

.form__input-date:focus,
.form__input-date:hover {
  outline: none;
  border-color: #367BCF;
}

.form-select {
  position: relative;
}

.input__label,
.select__label,
.inputs-radio__label,
.inputs-radio__title {
  margin: 0 0 10px 0;
  font-size: 19px;
  display: block;
  font-weight: 500;
}

.inputs-radio__container {
  margin: 15px 0;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.inputs-radio__container > label {
  cursor: pointer;
}

.inputs-radio__container:last-child {
  margin: 0;
}

.inputs-radio__container-title {
  font-weight: 500;
  margin-bottom: 20px;
}

.inputs-radio__button {
  transform: scale(1.5);
  margin-right: 15px;
  cursor: pointer;
}

.block-addition__container {

}

.form-submit {
  margin: 20px 0 0 0;
}

.form__block-addition__title {
  margin: 40px 0 25px 0;
  text-align: center;
  font-weight: 500;
}

.tournamentform__error{
  color: #F20505;
  font-size: 17px;
}

.form-description__container {
  padding: 20px;
  border: 1px solid #CCC;
  border-radius: 9px;
  /* margin: 0 20px 0 20px; */
}

.form-description__text, .form-description__bold {
  color: #3A3A3A;
  font-size: 19px;
  margin: 0 0 20px 0;
}

.form-description__bold {
  font-weight: 600;
}

.tournament-info {
  margin: 0 5px 70px 5px;
}