.popup-error__container {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.29);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: flex-end;
  z-index: 9999;
}

.popup-error__wrapper {
  background-color: #fff;
  border-radius: 11px;
  padding: 20px;
  margin: 20px 20px 40px 20px;
}

.popup-error__body {
  overflow-y: scroll;
  max-height: 450px;
  width: 100%;
  margin-bottom: 20px;
  font-size: 19px;
  font-weight: 500;
  text-align: center;
}

.popup-error__body::-webkit-scrollbar {
  width: 0;
}

.react-datepicker-wrapper:first-child {
  margin-bottom: 15px;
}