.venue-form__container {
  padding: 0 20px 70px 20px;
}

.venue-form__input {
  margin: 15px 0;
}

.venue-form__input-label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 19px;
}

.venue-form__search-field {
  border-radius: 9px;
}

.venue-form__error {
  color: #F20505;
}

.venue-form__submit {
  margin: 20px 0;
}

.geoapify-autocomplete-input {
  width: 100%;
  padding: 0 31px 0 10px;
  background-color: transparent;
  border: none;
  outline: none;

  line-height: 36px;
  height: 55px;

  font-size: 17px;
  box-sizing: border-box;
}

.geoapify-autocomplete-items {
  position: absolute;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-top: none;
  background-color: #fff;

  z-index: 99;
  top: 100%;
  left: 0;
  right: 0;
}

.geoapify-autocomplete-items div {
  padding: 10px;
  cursor: pointer;
}

.geoapify-autocomplete-items div:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.geoapify-autocomplete-items .active {
  background-color: rgba(0, 0, 0, 0.1);
}

.geoapify-autocomplete-item {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.geoapify-autocomplete-item .icon {
  display: inline-block;
  width: 40px;
  height: 24px;
  color: #aaa;
}

.geoapify-autocomplete-item .icon.emoji {
  color: unset;
  font-size: 20px;
  opacity: 0.9;
}

.geoapify-close-button {
  position: absolute;
  right: 5px;
  top: 0;

  height: 100%;
  display: none;
  align-items: center;
}

.geoapify-close-button.visible {
  display: flex;
}

.geoapify-close-button {
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.geoapify-close-button:hover {
  color: rgba(0, 0, 0, 0.6);
}

.geoapify-autocomplete-items .main-part .non-verified {
  color: #ff4848;
}

.geoapify-autocomplete-items .secondary-part {
  margin-left: 10px;
  font-size: 17px;
  color: rgba(0, 0, 0, 0.6);
}

.inputs-radio__separator{
  border-bottom: 1px solid #CCCCCC;
}

.venue-form__inputs-radio {
  margin-top: 20px;
}

.inputs-radio__button {
  transform: scale(1.3);
}

label {
  font-size: 17px;
}

.venue-form__error {
  color: #F20505;
}

.venue-form__main-error{
  margin: 20px 0;
  color: #F20505;
  text-align: center;
}