.roundrobin-grid__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.roundrobin-grid__title {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0;
  font-weight: 500;
}

.roundrobin-grid__wrapper {
  margin: 0 0 40px 0;
}

.roundrobin-grid__sheet {
  overflow: auto;
  overflow-y: hidden;
  width: 100%;
}
.roundrobin-grid__sheet::-webkit-scrollbar {
  width: 0;
}

.roundrobin-grid__content {
  display: table;
  border-collapse: collapse;
  width: max-content;
}

.roundrobin-grid__row {
  display: table-row;
}

.roundrobin-grid__header {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0 0 0 0;
  font-weight: 500;
  padding: 15px;
  display: table-cell;
  border: 1px solid #CCCCCC;
  text-align: center;
}

.roundrobin-grid__cell {
  display: table-cell;
  border: 1px solid #CCCCCC;
  text-align: center;
  color: #3A3A3A;
  font-size: 19px;
  margin: 0 0 0 0;
  font-weight: 400;
  padding: 15px;
  vertical-align: middle;
}
.roundrobin-grid__cell--grey {
  background-color: #F3F3F3;
}
.roundrobin-grid__cell > div {
  display: flex;
  justify-content: center;
}
.roundrobin-grid__cell > div > p {
  margin: 0 10px 0 0;
  cursor: pointer;
}
.roundrobin-grid__cell > div > p > span {
  color: #4694D9;
}
.roundrobin-grid__cell > div > p:last-child {
  margin: 0;
}