.olympicplus-grid__container {
  overflow: auto;
  margin-bottom: 20px;
}

.olympicplus-grid__title {
  font-size: 20px;
  font-weight: 500;
  margin: 10px 0;
}

.olympicplus-grid__columns {
  border-left: 1px solid #367BCF;
  display: flex;
}

.olympicplus-grid__column-title--first {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.olympicplus-grid__column-title {
  border-bottom: 1px solid #367BCF;
  border-right: 1px solid #367BCF;
  border-top: 1px solid #367BCF;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  font-size: 19px;
  font-weight: 500;
}

.olympicplus-grid__column-title-round--first {
  width: calc(100% - 40px);
}

.olympicplus-grid__column-title-symbole {
  border-right: 1px solid #367BCF;
  height: 100%;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.olympicplus-grid__column-container,
.olympicplus-grid__result {
  border-right: 1px solid #CCC;
}

.olympicplus-grid__column-container--first {
  border-right: 1px solid #367BCF;
}

.olympicplus-grid__cell {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #CCC;
  cursor: pointer;
}

.olympicplus-grid__cell--first {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #367BCF;
  cursor: pointer;
}

.olympicplus-grid__cell-number {
  width: 35px;
  text-align: center;
  font-size: 19px;
  font-weight: 500;
}

.olympicplus-grid__cell-container--first {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
  width: 100%;
}

.olympicplus-grid__cell-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
  width: 100%;
}

.olympicplus-grid__cell-players {
  height: 9vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.olympicplus-grid__cell-doubles {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.olympicplus-grid__cell-doubles-item {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.olympicplus-grid__cell-name,
.olympicplus-grid__result-name {
  font-size: 19px;
  padding: 5px;
  min-width: 90px;
  max-width: 120px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-left: 5px;
}

.olympicplus-grid__cell-name--doubles {
  font-size: 19px;
  min-width: 90px;
  max-width: 120px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-left: 5px
}

.olympicplus-grid__cell-name {
  margin-right: 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.olympicplus-grid__cell-noname {
  height: 50%;
  display: flex;
  align-items: center;
  padding: 0 5px;
  font-size: 19px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.olympicplus-grid__cell-result {
  display: flex;
  align-items: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.olympicplus-grid__cell-score {
  font-size: 19px;
  color: #4694D9;
  font-weight: 500;
  padding: 5px 0;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.olympicplus-grid__cell-tiebreak {
  font-size: 13px;
  cursor: pointer;
}

.olympicplus-grid__result-container {
  display: flex;
  align-items: center;
  padding-right: 5px;
}

.olympicplus-grid__result-container:nth-child(2n) {
  background-color: #EBF4FF;
  border-bottom: 1px solid #CCC;
}

.olympicplus-grid__result-place {
  text-align: center;
  font-size: 19px;
  width: 35px;
}

.olympicplus-grid__result-name {
  padding: 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

::-webkit-scrollbar {
  display: none;
}