.guest-link__container {
  border: 1px solid #CCCCCC;
  display: flex;
  padding: 10px 13px 10px 13px;
  margin: 20px 0 20px 0;
  border-radius: 10px;
  align-items: center;
  justify-content: space-between;
}

.guest-link__text {
  color: #4694D9;
  font-size: 17px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.guest-link__button {
  border: 0;
  background-color: transparent;
  padding: 0;
  margin: 0 0 0 13px;
}

.copy-link__icon {
  width: 25px;
  height: 25px;
}