.header__container {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px;
	border-bottom: 1px solid #CCCCCC;
}

.header__wrapper {
	display: flex;
	align-items: center;
	margin: 0px;
	width: 100%;
	justify-content: space-between;
}

.icon__container {
	width: 30px;
	height: 30px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.back-icon {
	height: 30px;
	width: 30px;
	cursor: pointer;
}

.settings-icon,
.translation-icon {
	height: 25px;
	width: 25px;
	cursor: pointer;
}

.header__title {
	color: #3A3A3A;
	font-size: 20px;
	font-weight: 500;
	text-align: center;
	margin: 0 10px 0 10px;
	white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.header__subcontainer--grid{
	width: 100%;
	display: grid;
	grid-template-columns: 33% 33% 33%;
}

.header__subtitle {
	grid-column-start: 2;
	width: 100%;
	color: #3A3A3A;
	font-size: 19px;
	margin: 10px 0 0 0;
	font-weight: 400;
	text-align: center;
}

.header__structure{
	grid-column-start: 3;
	color: #367BCF;
	font-size: 19px;
	margin: 10px 0 0 0;
	font-weight: 500;
	text-align: end;
}