.translator-popup {
  padding: 0 0 20px 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #CCCCCC;
}

.translator-popup:last-child {
  padding: 20px 0 0 0;
  border: none;
}

.translator-popup__inputs-radio{
  height: 25px;
  width: 25px;
  margin-right: 15px;
}

.translator-popup > label{
  font-size: 20px;
  width: 100%;
  cursor: pointer;
}