.stage-form__wrapper {
  width: 100%;
}

.stage-form__container {
  position: relative;
}

.stage-form__button-container {
  display: flex;
  justify-content: flex-end;
}

.stage-form__button-edition-mode {
  width: 40px;
  height: 40px;
  font-size: 17px;
  color: #4694D9;
  font-weight: 500;
  cursor: pointer;
}

.stage-form__edition-mode{
  position: absolute;
  top: 0px;
  right: 0px;
  height: 28px;
  width: 28px;
}

.stage-form__level{
  padding-top: 35px;
}

.stage-form__level-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.stage-form__level-name {
  font-weight: 500;
  font-size: 22px;
}

.delete-icon {
  cursor: pointer;
}

.stage-form__group {
  margin-bottom: 20px;
  border: 1px solid #CCCCCC;
  border-radius: 9px;
  padding: 15px 15px 0 15px;
}

.stage-form__button-level-addition{
  margin: 20px 0 30px 0;
}

.stage-form__participants-list{
  padding-top: 20px;
}

.stage-form__participants-list-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.stage-form__participants-list-title{
  font-size: 20px;
  font-weight: 500;
}

.stage-form__participants-list-container{
  border: 1px solid #CCC;
  border-radius: 11px;
  margin-top: 10px;
}

.stage-form__participants-list-player{
  border-bottom: 1px solid #CCC;
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-player--chosen{
  border: 3px solid #4694D9;
  padding: 15px;
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-player:first-child{
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-player:last-child{
  border: none;
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}

.stage-form__participants-list-player--chosen:first-child{
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-player--chosen:last-child{
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}


.stage-form__participants-list-container-doubles{
  padding: 15px;
}

.stage-form__participants-list-doubles{
  padding:  0px 10px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #CCCCCC;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-doubles:first-child{
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-doubles:last-child{
  border: none;
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}

.stage-form__participants-list-doubles--chosen{
  padding:  10px 15px;
  display: flex;
  align-items: center;
  border: 3px solid #4694D9;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-doubles--chosen:first-child{
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-doubles--chosen:last-child{
  border: none;
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}

.stage-form__participants-list-doubles-raiting{
  font-size: 20px;
  margin-right: 15px;
}

.stage-form__participants-list-doubles-name{
  font-size: 20px;
}

.stage-form__participants-list-doubles-name:first-child{
  margin-bottom: 5px;
}

.stage-form__button {
  box-sizing: border-box;
  width: calc(100vw - 15px);
  position: fixed;
  padding: 0 10px;
  bottom: 120px;
  left: 0px;
  right: 0px;
  margin: auto;
}

.stage-form__container-deletion{
  display: flex;
  justify-content: center;
  margin: 25px 0;
}

.stage-form__button-deletion{
  color: #FF0101;
  border: none;
  background-color: transparent;
  font-size: 17px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
}