.login__container {
  padding: 20px 20px 0 20px;
}

.login__form{
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.login__input-label{
  display: block;
  font-size: 19px;
  margin-top: 15px;
}

.login__input-login, .login__input-password{
  margin-top: 5px;
}

.login__input-icon{
  width: 23px;
  margin: 0;
  z-index: 1;
  position: absolute;
  right: 20px;
  top: 60%;
  cursor: pointer;
}

.login__error-login{
  color: #FF0101;
}

.login__error-password{
  color: #FF0101;
}

.login__form-error{
  color: #FF0101;
  text-align: center;
  font-size: 18px;
  margin-top: 15px;
}

.login_button-submit{
  margin-top: 20px;
}

.login__password-restoration{
  color: #FF0101;
  text-align: center;
  width: 100%;
  display: block;
  padding: 10px 0 10px 0;
}

.registration-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 40px 0 0 0;
  padding: 18px 0 18px 0;
  cursor: pointer;
  background-color: #fff;
  color: #367BCF;
  font-size: 19px;
  border: 1px solid #367BCF;
  border-radius: 9px;
}