.stage__wrapper {
  width: 100%;
}

.stage__container {
  padding: 10px;
  margin-bottom: 160px;
}

.stage-level__name {
  font-weight: 500;
  font-size: 22px;
  margin: 0;
}

.stage-group__header {
  display: flex;
  align-items: center;
}

.stage-group__name {
  font-weight: 500;
  font-size: 19px;
  margin: 0 15px 15px 0;
}

.stage-group__type {
  box-sizing: border-box;
  height: 45px;
  width: 100%;
  border: 1px solid #E0E0E0;
  border-radius: 11px;
  line-height: 2.5;
  padding-left: 10px;
  font-size: 18px;
  color: #3A3A3A;
  margin: 5px 0 10px;
}

.stage-group {
  margin-bottom: 20px;
}

.stage__button {
  box-sizing: border-box;
  width: 100vw;
  position: fixed;
  padding: 0 10px;
  bottom: 90px;
  left: 0;
  right: 0;
  margin: auto;
}