.tournaments-container {
  height: 100%;
}

.tournaments-wrapper {
  width: 100%;
}

.tournaments__content{
  padding: 20px 20px 0 20px;
}

.no-tournaments__container{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: inherit;
  margin: 0 20px 70px 20px;
  justify-content: center;
}

.no-tournaments__text{
  text-align: center;
  font-size: 20px;
  color: #3A3A3A;
  font-weight: 400;
}

.tournaments-calendar__container {
  border: 1px solid #CCCCCC;
  border-radius: 11px;
  padding: 13px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
  cursor: pointer;
}

.tournaments-calendar__title {
  font-size: 17px;
}

.tournaments-calendar__icon {
  width: 25px;
  height: 25px;
}

.filter__button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0 0 0;
}

.filter__button {
  color: #fff;
  font-size: 17px;
  margin: 0;
  font-weight: 600;
  border: 1px solid #4694D9;
  border-radius: 11px;
  background-color: #4694D9;
  padding: 14px 0 14px 0;
  width: 45%;
}

.filter__button:first-child {
  background-color: #fff;
  color: #4694D9;
}

.tournaments__button-addition{
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 20px;
  bottom: 80px;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  background-color: #4694D9;
  cursor: pointer;
}

.tournamnet-creation__header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px 0 20px;

  .tournamnet-creation__icon-cross {
    cursor: pointer;
  }
}

.tournamnet-creation__title{
  font-size: 20px;
}

.no-players__container {
  padding: 20px;
  border: 1px solid #CCCCCC;
  border-radius: 11px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 0 20px 0;
}

.no-players__container > h2 {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0 0 10px 0;
  font-weight: 500;
}

.no-players__description {
  color: #3A3A3A;
  font-size: 19px;
  margin: 0 0 0 0;
}

.no-players__description > a {
  color: #367bcf;
}