.account__wrapper {
  padding: 20px 20px 0 20px;
}

.account__block:first-child {
  margin: 0 0 40px 0;
}

.account__block-title {
  color: #3A3A3A;
  font-size: 21px;
  margin: 0 0 20px 0;
  font-weight: 500;
}

.account__communities-content, .account__menu {
  border-radius: 11px;
  border: 1px solid #CCCCCC;

}

.account__communities, .account__menu-links  {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
}

.account__communities-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 15px;
  box-sizing: border-box;
}

.account__communities-container__radio-input{
  height: 20px;
  width: 20px;
  margin-right: 15px;
}

.account__communities--border, .account__menu-links--border{
  border-width: 0px 0px 1px 0px;
  border-style: solid;
  border-color: #CCCCCC;
  border-radius: 0;
}

.account__communities--border:last-child {
  border: 0;
}

.account__communities-item__name, .account__menu-links__item-subtitle {
  color: #3A3A3A;
  font-size: 19px;
  margin: 0;
  font-weight: 400;
}

.account__communities-item__name-input{
  color: #3A3A3A;
  font-size: 19px;
  margin: 0;
  font-weight: 400;
  border: none;
  width: 70%;
  padding: 10px;
  background-color: #EBF4FF;
  border-radius: 11px;
  font-family: 'Rubik';
  cursor: pointer;
}

.account__icons-edition{
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.account__icon-done, .account__icon-close{
  height: 33px;
  width: 33px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  cursor: pointer;
}

.account__icon-done{
  background-color: #EBF4FF;
}

.account__icon-close{
  background-color: #f0efef;
  margin-left: 10px;
}


.account__menu-links__item-icon {
  max-width: 20px;
  max-height: 20px;
}

.edit-icon {
  width: 33px;
  max-height: 33px;
  cursor: pointer;
}

