.group-player__number{
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: #EBF4FF;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin-right: 15px;
}

.group-player__single-name{
  width: 100%;
  font-size: 20px;
  padding: 15px 0 15px 0;
  cursor: pointer;
}

.group-player__double-rating{
  font-size: 20px;
  width: 65px;
}

.group-player__double{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 64px;
}

.group-player__double-noplayer{
  height: 60px;
  font-size: 20px;
  display: flex;
  align-items: center;
}

.group-player__double-name{
  width: 100%;
  font-size: 20px;
  height: 30px;
}