.settings-account__container {
  padding: 20px 20px 0 20px;
  display: flex;
  flex-direction: column;
  margin-bottom: 90px;
}

.settings-account__wrapper {
  border: 1px solid #CCCCCC;
  border-radius: 11px;
}

.settings-account__block {
  border-bottom: 1px solid #CCCCCC;
  padding: 20px;
}

.settings-account__block--edit {
  display: flex;
  justify-content: space-between;
}

.settings-account__block:last-child {
  border: 0;
}

.settings-account__text {
  color: #3A3A3A;
  font-size: 19px;
  margin: 0;
  font-weight: 400;
}

.settings-account__confirmation {
  color: #A4C9F5;
  font-size: 19px;
  margin: 0;
  font-weight: 400;
}

.settings-account__logout {
  color: #FF0101;
  font-size: 19px;
  margin: 30px 0 0 0;
  font-weight: 500;
  border: 1px solid #FF0101;
  border-radius: 11px;
  padding: 20px;
  text-align: center;
}

.settings-account__delete {
  color: #FF0101;
  font-size: 17px;
  margin: 30px 0 0 0;
  font-weight: 400;
  text-align: center;
  cursor: pointer;
}

.settings-account__info {
  color: #367BCF;
  font-size: 19px;
  margin: 0;
  font-weight: 400;
  text-align: center;
  bottom: 100px;
  margin-top: 30px;
}