.registration__container {
	padding: 20px 20px 0 20px;
}

.checkbox-input {
	width: 30px;
	height: 30px;
	margin: 20px 15px 0 0;
	cursor: pointer;
}

.checkbox-container {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin: 0 0 20px 0;
	cursor: pointer;
}

.registration__input-name {
	margin-top: 5px;	
}

.registration__error {
	color: #FF0101;
}

.registration__input-label {
	display: block;
	font-size: 19px;
	margin-top: 15px;
	cursor: pointer;
}

.registration__form-input {
	
}

.registration__form {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
}