.tournament-info {

}

.tournamentform-container {
  padding: 0;
}

.tournament-info__header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.tournament-info__header-title{
  font-size: 20px;
  font-weight: 600;
}

.tournamentform-container {
  /* padding: 0 5px 0 5px; */
  margin: 0;
}

.tournament-info__header-edition{
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.close-icon {
  cursor: pointer;
}

.info-description{
  font-size: 20px;
  line-height: 1.4;
  padding: 15px;
  border: 1px solid #CCCCCC;
  border-radius: 11px;
  max-height: 100%;
}

.tournament-info__button-submit{
  margin-top: 20px;
}

.delete-tournamet__container {
  display: flex;
  justify-content: center;
  margin: 40px 0 0 0;
}

.delete-tournamet__button {
  color: #FF0101;
  font-size: 19px;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  background-color: #fff;
  border: 0;
  cursor: pointer;
  text-align: center;
}
