.match-group__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.match-group__title {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0;
  font-weight: 500;
}

.match-table__container {
  border: 1px solid #CCCCCC;
  display: table;
  border-collapse: collapse;
  width: 100%;
  margin: 0 0 40px 0;
}

.match-table__body {
  border-bottom: 1px solid #CCCCCC;
  display: table-row;
}

.match-table__content {
  padding: 15px;
  border-right: 1px solid #CCCCCC;
  display: table-cell;
  text-align: center;
  color: #3A3A3A;
  font-size: 19px;
  margin: 0 0 0 0;
  font-weight: 400;
  vertical-align: middle;
}
.match-table__content > p {
  border-bottom: 1px solid #CCCCCC;
  padding: 0 0 10px 0;
  margin: 0 0 10px 0;
}
.match-table__content > p > span {
  color: #4694D9;
}
.match-table__content > p:last-child {
  border: 0;
  padding: 0;
  margin: 0;
}

.match-table__content > div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  border-bottom: 1px solid #CCCCCC;
  padding: 0 0 10px 0;
  margin: 0 0 10px 0;
}
.match-table__content > div:last-child {
  border: 0;
  padding: 0;
  margin: 0;
}
.match-table__content > div > p {
  margin: 0 5px 0 0;
  cursor: pointer;
}
.match-table__content > div > p:last-child {
  margin: 0;
}
.match-table__content > div > p > span {
  color: #4694D9;
}